import { Component, ElementRef, ViewChild } from '@angular/core';
import { AccountActivityStatus } from '../dashboard/dashboard.component';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
declare var bootstrap: any;

@Component({
  selector: 'app-not-approved-user',
  standalone: true,
  imports: [CommonModule, FormsModule],
  templateUrl: './not-approved-user.component.html',
  styleUrl: './not-approved-user.component.scss'
})
export class NotApprovedUserComponent {
  users: any[] = [];              // ðŸ‘ˆ currently displayed users
  allApprovedUsers: any[] = [];   // ðŸ‘ˆ full approved list
  AccountActivityStatus = AccountActivityStatus;
  searchText: string = '';
  pageSize: number = 5;
  currentPage: number = 1;
  toastMessage: string = '';
  toastType: 'success' | 'danger' | 'warning' = 'success';
  allSelected: boolean = false;
  private userIdToDelete: number | null = null;

  pagination = {
    total: 0,
    page: 1,
    limit: 8
  };

  constructor(private http: HttpClient) { }

  @ViewChild('toastElement') toastElement!: ElementRef;

  ngOnInit(): void {
    this.loadUsers();
  }

  // âœ… fetch once, filter approved
  loadUsers(): void {
    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });

    // fetch all users (you may need to adjust limit if backend restricts it)
    this.http.get<any>(`${environment.apiUrl}/admin/users?limit=10000&isActive=true`, { headers })
      .subscribe({
        next: (res) => {
          // keep only approved
          this.allApprovedUsers = res.data.data.filter(
            (user: any) => user.status === this.AccountActivityStatus.CREATED
          );

          this.pagination.total = this.allApprovedUsers.length;
          this.pagination.limit = this.pageSize;

          this.updatePage();
        },
        error: (err) => {
          console.error('Error fetching users:', err);
        }
      });
  }

  // âœ… slice approved data according to pagination
  updatePage(): void {
    const start = (this.currentPage - 1) * this.pageSize;
    const end = start + this.pageSize;
    this.users = this.allApprovedUsers.slice(start, end);
  }

  get totalPages(): number {
    return Math.ceil(this.pagination.total / this.pageSize);
  }

  changePage(page: number) {
    if (page >= 1 && page <= this.totalPages) {
      this.currentPage = page;
      this.updatePage(); // ðŸ‘ˆ now no API call, just slice locally
    }
  }

  goPrevious() {
    this.changePage(this.currentPage - 1);
  }

  goNext() {
    this.changePage(this.currentPage + 1);
  }

  onPageSizeChange(event: any) {
    this.pageSize = +event.target.value;
    this.currentPage = 1;
    this.updatePage();
  }

  approveUser(user: any): void {
    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({ Authorization: `Bearer ${token}` });

    const body = {
      users: [{ id: user.id }],
      status: 'approved'
    };

    this.http.put(`${environment.apiUrl}/admin/users`, body, { headers })
      .subscribe({
        next: () => {
          this.showToast(`User ${user.doptOfficer?.englishName} approved successfully`, 'success');
          this.loadUsers();
        },
        error: (err) => {
          console.error('Error approving user:', err);
          this.showToast('Failed to approve user', 'danger');
        }
      });
  }

  rejectUser(user: any): void {
    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({ Authorization: `Bearer ${token}` });

    const body = {
      users: [{ id: user.id }],
      status: 'created'
    };

    this.http.put(`${environment.apiUrl}/admin/users&isActive=true`, body, { headers })
      .subscribe({
        next: () => {
          this.showToast(`User ${user.doptOfficer?.englishName} Moved to pending list`, 'warning');
          this.loadUsers();
        },
        error: (err) => {
          console.error('Error approving user:', err);
          this.showToast('Failed to approve user', 'danger');
        }
      });
  }

  showToast(message: string, type: 'success' | 'danger' | 'warning') {
    this.toastMessage = message;
    this.toastType = type;

    const toastBootstrap = new bootstrap.Toast(this.toastElement.nativeElement);
    toastBootstrap.show();
  }

  toggleSelectAll(event: any) {
    this.allSelected = event.target.checked;
    this.users.forEach(user => user.selected = this.allSelected);
  }

  checkIfAllSelected() {
    this.allSelected = this.users.every(user => user.selected);
  }

  hasSelectedUsers(): boolean {
    return this.users.some(user => user.selected);
  }

  bulkApprove(): void {
    const selectedUsers = this.users.filter(user => user.selected);

    if (selectedUsers.length === 0) {
      this.showToast('No users selected for approval', 'warning');
      return;
    }

    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({ Authorization: `Bearer ${token}` });

    const body = {
      users: selectedUsers.map(u => ({ id: u.id })),
      status: 'approved'
    };

    this.http.put(`${environment.apiUrl}/admin/users`, body, { headers })
      .subscribe({
        next: () => {
          this.showToast(`${selectedUsers.length} users approved successfully`, 'success');
          this.loadUsers();
        },
        error: (err) => {
          console.error('Error bulk approving users:', err);
          this.showToast('Bulk approval failed', 'danger');
        }
      });
  }

  openDeleteModal(userId: number) {
    this.userIdToDelete = userId;
    const modalEl = document.getElementById('confirmDeleteModal');
    if (modalEl) {
      const modal = new bootstrap.Modal(modalEl);
      modal.show();
    }
  }

  // Called when "Deactivate" in modal is clicked
  confirmDelete() {
    if (!this.userIdToDelete) return;

    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({ Authorization: `Bearer ${token}` });

    const body = {
      isActive: false,
      account: { id: this.userIdToDelete }
    };

    this.http.patch(`${environment.apiUrl}/admin/user`, body, { headers })
      .subscribe({
        next: () => {
          this.showToast('User Account Deactivated successfully', 'success');
          this.loadUsers(); // refresh list
        },
        error: (err) => {
          this.showToast('Error Deactivating account', 'danger');
        }
      });

    // Close modal
    const modalEl = document.getElementById('confirmDeleteModal');
    if (modalEl) {
      const modal = bootstrap.Modal.getInstance(modalEl);
      modal?.hide();
    }

    this.userIdToDelete = null;
  }
}
